
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Sticky from 'vux/src/components/sticky/index.vue'

import serviceList from '../../components/ServiceList.vue';
import goodsInService from '../../components/GoodsInService.vue';
import tools from '../../lib/tools.js';
export default {
name:'tagServices',
components:{Tab, TabItem, Sticky,serviceList,goodsInService},
data(){
return {
tabItems:['推荐商品','推荐服务'],
selectTag:'推荐服务',
keywordsName:'',
serviceList:[],
goodList:[]
}
},
created(){
this.keywordsName = tools.getRequestParam('keywords');
if(!this.keywordsName){
return false;
}
this.toggleTab('推荐商品');
},
methods:{
toggleTab(item){

this.selectTag = item;
let type = '1';
if(item=='推荐商品'){
type = '0';
}
let vm = this;
this.$http.post('/service/findServiceByKeywords',{currentPage:1,showCount:10000,keywordsName:vm.keywordsName,type:type}).then((result) => {
console.log(result);
result = result.data;
if(result.successful && result.status==200){
if(type=='0'){
vm.goodList=result.items;
vm.serviceList = [];
}else{
vm.serviceList=result.items;
vm.goodList = [];
}
}else{
alert('查询出错');
}
},(result) => {
console.error(result);
});
}
}
}



